@import url('https://fonts.googleapis.com/css2?family=Comfortaa&family=Monoton&display=swap');

:root{

    --primary1-light:#F5F5D8;
    --primary1:#FFEC3F;
    --primary1-dark: #CA8207;
    --primary2-dark1:#092140;
    --primary2-dark2: #12489F;
    --primary2-dark3:#1671C0;
    --primary2-dark4:#40B3FA;
}
.App {
  text-align: center;
  width: 100%;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}


.nav{
  height: 50px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 0.000015px 3px 2px rgba(0, 0, 0, 0.234);
  z-index: 3;
}
.nav-sns{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  width: 15%;
  margin-right: 100px;
}
.wepo-logo{
  width: 100px;
  margin-left: 50px;
}

.nav a{
  text-decoration: none;
  color: black;

}
.nav a:hover, .fa-brands:hover{
  color: var(--primary2-dark4);
  cursor: pointer;
  transition: 0.2s;
}

.hero-container{
  position: relative;
  width: 100%;
  height: 50vh;
  background: linear-gradient(90deg,white 40%,#ffec3f4d 90% ) ;

}
.hero-container img{
  top: 0px;
  left: 0px;
  width:100%;
  opacity: 0.0;
  height: 30vh;
}
.tagline{
  padding: 0px 30px 0px 100px;
  position: relative;
  display: flex;
  width:40%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.tagline-left{
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tagline h1{
  font-size: 60px;
}
.tag-list{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: space-between;
}
.tag-list li{
  font-size: 30px;
  margin: 0px 20px 0px 20px;
}